@mixin font-face($family, $file, $weight: 400, $style: normal) {
  @font-face {
    font-family: "#{$family}";
    src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
      url("../fonts/#{$file}.woff") format("woff");
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
  }
}

@mixin pseudo($display: block, $pos: absolute, $content: "") {
  content: $content;
  display: $display;
  position: $pos;
}

@mixin scrollbar($thumb: #fff, $track: transparent, $margin: 0px) {
  scrollbar-width: thin;
  scrollbar-color: $thumb $track;

  &::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    border-radius: 8px;
  }

  &::-webkit-scrollbar-track {
    background: $track;
    border-radius: 8px;
    margin: $margin 0;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $thumb;
    border-radius: 8px;
  }
}

@mixin scrollbarClear() {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;

  &::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    border-radius: 0px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: transparent;
    border-radius: 0px;
  }
}
