@use "base/mixins" as mixins;

// Подключаем все начертания Kazimir
@include mixins.font-face("Kazimir", "Kazimir-Light", 300, normal);
@include mixins.font-face("Kazimir", "Kazimir-LightItalic", 300, italic);
@include mixins.font-face("Kazimir", "Kazimir-Regular", 400, normal);
@include mixins.font-face("Kazimir", "Kazimir-RegularItalic", 400, italic);
@include mixins.font-face("Kazimir", "Kazimir-Medium", 500, normal);
@include mixins.font-face("Kazimir", "Kazimir-MediumItalic", 500, italic);
@include mixins.font-face("Kazimir", "Kazimir-Bold", 700, normal);
@include mixins.font-face("Kazimir", "Kazimir-BoldItalic", 700, italic);
@include mixins.font-face("Kazimir", "Kazimir-Black", 900, normal);
@include mixins.font-face("Kazimir", "Kazimir-BlackItalic", 900, italic);
